import Sidebar from "./components/sidebar/Sidebar";
import Topbar from "./components/topbar/Topbar";
import "./App.css";
import Home from "./pages/home/Home";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import VehicleList from "./pages/vehicleList/VehicleList";
import Vehicle from "./pages/vehicle/Vehicle";
import NewVehicle from "./pages/newVehicle/NewVehicle";
import EnquiryList from "./pages/enquiryList/EnquiryList";
import Enquiry from "./pages/enquiry/Enquiry";
// import Vehicle from "./pages/vehicle/Vehicle";
import NewEnquiry from "./pages/newEnquiry/NewEnquiry";
import Login from './pages/auth/Login'
import React from "react";

// import 'bootstrap/dist/css/bootstrap.min.css';

class App extends React.Component {

  isLoggedIn = localStorage.getItem('accessToken');

  render = () => {

    if (!this.isLoggedIn) {
      return (
        <Router>
          <Topbar />
          <Login />
        </Router>
      )
    }

    return (
      <Router>
        <Topbar />
        <div className="container">
          <Sidebar />
          <Switch>
            <Route exact path="/">
              <Home />
            </Route>
            <Route path="/vehicles">
              <VehicleList />
            </Route>
            <Route path="/vehicle/:vehicleId">
              <Vehicle />
            </Route>
            <Route path="/newVehicle">
              <NewVehicle />
            </Route>
            <Route path="/enquiries ">
              <EnquiryList />
            </Route>
            <Route path="/enquiry/:enquiryId">
              <Enquiry />
            </Route>
            <Route path="/newenquiry">
              <NewEnquiry />
            </Route>
          </Switch>
        </div>
      </Router>
    );
  }
}

export default App;
